/* Button.module.css */

.button {
    background-color: #007bff;
    color: white;
    border: 2px solid #007bff;
    border-radius: 8px;
    padding: 12px 24px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    text-align: center;
  }
  
  .button:hover {
    background-color: white;
    color: #007bff;
    border-color: #007bff;
  }
  
  .button:active {
    background-color: #0056b3;
    border-color: #004085;
    color: white;
  }
  
  .button:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(38, 143, 255, 0.5);
  }
  