.header {
    background-color: #282c34;
    color: white;
    padding: 1rem;
    text-align: center;
  }
  
  .nav {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0;
  }
  
  .navItem {
    margin: 0 1rem;
  }
  
  .navItem a {
    color: white;
    text-decoration: none;
    font-weight: bold;
  }
  
  .navItem a:hover {
    text-decoration: underline;
  }
  